@keyframes fadeInUp {
  100% {
    bottom: 0; } }

.select-level-1,
.select-level-d,
.select-level-3 {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.26);
  transition: all .5s;
  z-index: 2; }
  .select-level-1 .select-container,
  .select-level-d .select-container,
  .select-level-3 .select-container {
    height: 13.75rem;
    box-sizing: border-box;
    background-color: #fff;
    overflow: auto;
    text-align: center;
    padding-left: 0;
    padding-right: 0;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -100%;
    animation: fadeInUp .3s ease-out;
    animation-fill-mode: forwards; }
    .select-level-1 .select-container .select-btns,
    .select-level-d .select-container .select-btns,
    .select-level-3 .select-container .select-btns {
      display: flex;
