      height: 2.8125rem;
      width: 100%;
      background-color: #eee;
      padding: 0 5%;
      align-items: center;
      border-bottom: 1px solid #efefef; }
    .select-level-1 .select-container .select-main,
    .select-level-d .select-container .select-main,
