      .select-level-d .select-container .select-main:after,
      .select-level-3 .select-container .select-main:before,
      .select-level-3 .select-container .select-main:after {
        content: '';
        display: block;
        width: 100%;
        height: 39%;
        position: absolute; }
      .select-level-1 .select-container .select-main:before,
      .select-level-d .select-container .select-main:before,
      .select-level-3 .select-container .select-main:before {
        top: 0;
