        .select-level-1 .select-container .select-main .swiper-container .swiper-slide-prev, .select-level-1 .select-container .select-main .swiper-container .swiper-slide-next,
        .select-level-d .select-container .select-main .swiper-container .swiper-slide-prev,
        .select-level-d .select-container .select-main .swiper-container .swiper-slide-next,
        .select-level-3 .select-container .select-main .swiper-container .swiper-slide-prev,
